/* ==========================================================================
   WooCommerce Quantity Inputs responsive styles - begin
   ========================================================================== */
@include laptop-landscape-large {

    .eltdf-woo-single-page {

        .eltdf-single-product-summary {

            padding: 0 0 0 33px;
        }
    }
}
@include laptop-landscape-mac {

    .eltdf-woo-single-page {

        .eltdf-single-product-summary {

            .eltdf-quantity-buttons {

                margin: 0 5px 0 0;

            }
        }
    }
    .woocommerce-page {
        
        .eltdf-content {

            .eltdf-quantity-buttons {

                .eltdf-quantity-label {

                     margin-right: 20px;
                }
            }
        }
    }
}


@include ipad-portrait {

    .woocommerce-page .eltdf-content,
    div.woocommerce {

        .eltdf-quantity-buttons {

            .eltdf-quantity-input,
            .eltdf-quantity-minus,
            .eltdf-quantity-plus {
                font-size: 13px;
            }

            .eltdf-quantity-input {
                height: 38px;
                width: 38px;
                line-height: 36px;
            }

            .eltdf-quantity-minus,
            .eltdf-quantity-plus {
                width: 19px;
                height: 19px;
                line-height: 17px;
            }
        }
    }
}   
/* ==========================================================================
   WooCommerce Quantity Inputs responsive styles - end
   ========================================================================== */