/* ==========================================================================
   WooCommerce Cart page responsive styles - begin
   ========================================================================== */

@include ipad-portrait {
	
	.eltdf-woocommerce-page {
		
		table.cart {
			.eltdf-quantity-buttons {
			    border: none;
			    margin: 0;
			    padding: 0;
			    width: 104px;
			    padding: 0 10px;
			    line-height: 64px;

			    .eltdf-quantity-input {
		    	    width: 29px;
			    }
			}
			tr {
				td.product-quantity {
					.eltdf-quantity-buttons {
						.eltdf-quantity-label {
							display: none;
						}
					}
				}
			}
			
			td.actions {
				
				.coupon {
					
					input[type="text"] {
						width: 180px;
						margin: 0 17px 0 0;
						padding: 9px 20px;
					}
				}
				 
			}
		}
	}
}

@include phone-landscape {
	
	.eltdf-woocommerce-page {
		.cart-collaterals {
			.wc-proceed-to-checkout {
				a {
				    width: 100%;
    				text-align: center;
				}
			}
		}
		
		table.cart {
			
			tr {
				
				th, td {
					font-size: 12px;
				}
				
				&.cart_item {
					
					td {
						
						&.product-thumbnail {
							width: auto;
							padding-right: 0;
							
							img {
								display: none;
							}
						}
					}
				}
			}
			
			td.actions {
				
				.coupon {
					margin: 0 0 20px;
					
					input[type="text"] {
						width: 160px;
						padding: 9px 15px;
						margin: 0 10px 20px 0;
						float: none;
						display: block;
						width: 100%;
					}
				}
				
				.coupon,
				button,
				> input {
					float: none;
					display: block;
					width: 100% !important;
				}
			}
		}
	}
}

/* ==========================================================================
   WooCommerce Cart page responsive styles - end
   ========================================================================== */