/* ==========================================================================
   WooCommerce Single Product Page Responsive styles - begin
   ========================================================================== */

@media only screen and (min-width: $ipad-landscape-plus-pixel) and (max-width: $laptop-landscape) {
    
    .eltdf-woo-single-page {
        
        .woocommerce-tabs {
            
            ul.tabs {
                
                > li {
                    
                    a {
                        padding: 12px 7px;
                    }
                }
            }
        }
    }
}

@include ipad-landscape {
    
    .eltdf-woo-single-page {
        
        .eltdf-single-product-content {
            
            .images,
            .eltdf-single-product-summary {
                float: none;
                width: 100%;
                padding: 0;
            }
	        
	        .images {
		
		        .woocommerce-product-gallery__trigger {
			        right: 10px;
		        }
	        }
            
            .eltdf-single-product-summary {
                margin: 30px 0 0;
            }
        }
        
        .woocommerce-tabs {
            
            ul.tabs {
                
                > li {
                    
                    a {
                        padding: 13px 10px;
                    }
                }
            }
        }
    }
}
@include ipad-portrait {

    .eltdf-woo-single-page {

        .woocommerce-tabs {

            ul.tabs {

                >li {
                    margin: 0 15px 0 0;
                }
            }
        }
    }
}

@include phone-landscape {
    
    .eltdf-woo-single-page {

        .woocommerce-tabs {
            margin: 12px 0 0;

            ul.tabs {

                >li {
                    display: block;
                    text-align: left;
                    &:after {
                        display: none;
                    }
                    &.active {
                        a {
                            border-bottom: 1px solid $first-main-color;
                        }
                    }
                }
            }
            .entry-content {
                > p {
                    text-align: left;
                }
            }
        }
    }
    .eltdf-woo-single-page {

        .eltdf-single-product-summary  {

            .eltdf-quantity-buttons {
                margin: 0 30px 25px 0;
            }


        }
    }
   

    .eltdf-woo-single-page {
	
	    &.eltdf-woo-single-thumb-on-left-side {
		
		    .eltdf-single-product-content {
			
			    .images {
				
				    &.woocommerce-product-gallery--columns-4 {
					
					    .woocommerce-product-gallery__image {
						
						    &:not(:first-child) {
							    width: 50%;
						    }
					    }
				    }
			    }
		    }
	    }
        
        .eltdf-single-product-content {
            
            .images {
    
                &.woocommerce-product-gallery--columns-4 {
        
                    .woocommerce-product-gallery__image {
            
                        &:not(:first-child) {
	                        width: 50%;
                        }
                    }
                }
            }
        }
    }
}
/* ==========================================================================
   WooCommerce Single Product Page Responsive styles - end
   ========================================================================== */